import "./App.css";
import Vorspeisen from "./Components/Vorspeisen";
import Hauptspeisen from "./Components/Hauptspeisen";
import Nachspeisen from "./Components/Nachspeisen";
import Menuleiste from "./Components/Menuleiste";
import { Link, Route, BrowserRouter as Router, Switch } from "react-router-dom";

if ("serviceWorker" in navigator) {
  navigator.serviceWorker
    .register("./service-worker.js")
    .then(function () {
      console.log("Der Service Worker wurde erfolgreich registriert");
    })
    .catch(function (error) {
      console.log(
        "Die Registrierung des Service Workers ist fehlgeschlagen mit dem folgenden Fehler: " +
          error
      );
    });
}

function App() {
  return (
    <div className="App">
      <Router>
        <header className="App-header">
          <Menuleiste />
          <p className="Nachricht">Viel Freude beim Durchstöbern unserer Rezepte!</p>
        </header>
        <main>
        </main>
        <Switch>
          <Route path="/vorspeisen" component={Vorspeisen}></Route>
          <Route path="/hauptspeisen" component={Hauptspeisen}></Route>
          <Route path="/nachspeisen" component={Nachspeisen}></Route>
        </Switch>
      </Router>
    </div>
  );
}

export default App;
