import React from "react";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import Typography from "@material-ui/core/Typography";
import Grid from "@material-ui/core/Grid";
import useFetch from "./useFetch";

export default function Vorspeisen() {
  const vorspeisen = useFetch(
    "https://www.brain2brain.de/RAW-WP/wp-json/wp/v2/vorspeisen"
  );

  return (    
    <Grid container spacing={2}>
      {vorspeisen &&
        vorspeisen.map((vorspeise, index) => (
          <Grid item xs={4} key={index}>
            <Card>
              <CardContent>
                <Typography
                  color="textSecondary"
                  gutterBottom
                  dangerouslySetInnerHTML={{ __html: vorspeise.title.rendered }}
                />
                <Typography
                  variant="body2"
                  component="p"
                  dangerouslySetInnerHTML={{ __html: vorspeise.content.rendered }}
                />
              </CardContent>
            </Card>
          </Grid>
        ))}
    </Grid>
  );
}

