import React from "react";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import Typography from "@material-ui/core/Typography";
import Grid from "@material-ui/core/Grid";
import useFetch from "./useFetch";

export default function Hauptspeisen() {
  const hauptspeisen = useFetch(
    "https://www.brain2brain.de/RAW-WP/wp-json/wp/v2/hauptspeisen"
  );

  return (    
    <Grid container spacing={2}>
      {hauptspeisen &&
        hauptspeisen.map((hauptspeise, index) => (
          <Grid item xs={4} key={index}>
            <Card>
              <CardContent>
                <Typography
                  color="textSecondary"
                  gutterBottom
                  dangerouslySetInnerHTML={{ __html: hauptspeise.title.rendered }}
                />
                <Typography
                  variant="body2"
                  component="p"
                  dangerouslySetInnerHTML={{ __html: hauptspeise.content.rendered }}
                />
              </CardContent>
            </Card>
          </Grid>
        ))}
    </Grid>
  );
}

