import React from "react";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import { fade, makeStyles } from "@material-ui/core/styles";
import { Link, Route, BrowserRouter as Router, Switch } from "react-router-dom";
import logo from "../images/logo.png";
import vorspeisenlogo from "../images/logovorspeisen.png";
import hauptspeisenlogo from "../images/logohauptspeisen.png";
import nachspeisenlogo from "../images/logonachspeisen.png";


const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    width: "100%",
  },
  farbe: {
    backgroundColor: "#8ab8c5",
  },
  div1: {
    float: "left",
    width: "33.33%",
    padding: "20px",
    fontFamily: "Source Sans Pro",
    color: "white",
  },
  bild: {
    width: "40%",
    height: "40%",
  },
  bild2: {
    width: "50%",
    height: "50%",
  },
}));

export default function Menuleiste() {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <AppBar position="static">
        <Toolbar className={classes.farbe}>
          <Link className={classes.div1} to="/">
            <img
              src={logo}
              className="App-logo"
              alt="Logo HTWK Köstlichkeiten"
            />
          </Link>
          <Link className={classes.div1} to="/vorspeisen">
            <img
              src={vorspeisenlogo}
              className={classes.bild}
              alt="Vorspeisenbild"
            />
          </Link>
          <Link className={classes.div1} to="/hauptspeisen">
            <img
              src={hauptspeisenlogo}
              className={classes.bild2}
              alt="Hauptspeisenbild"
            />
          </Link>
          <Link className={classes.div1} to="/nachspeisen">
            <img
              src={nachspeisenlogo}
              className={classes.bild}
              alt="Nachspeisenbild"
            />
          </Link>
        </Toolbar>
      </AppBar>
    </div>
  );
}
